import React from 'react'
import {useState} from 'react'
export default function Additem(props) {
    let myadditem ={
        display:"flex",
        justifyContent: "center",

    }
    const [title, settitle] = useState("")
    const [desc, setdesc] = useState("")
    const Submit=(e)=>{
        e.preventDefault();
        if(!title || !desc)
        {
            alert("Title and Description are not Empty")
        }
        else{
            props.addTodo(title,desc);
            settitle("");
            setdesc("");


        }




    }
    return (
        <div className="container-fluid my-3">
            <h3 className="text-center">Add Item in List</h3>
            <hr/>
            <form className="row g-3 my-4" style={myadditem} onSubmit={Submit} >
                <div className="col-4">
                    <label htmlFor="title" className="visually-hidden">Title</label>
                    <input type="text" className="form-control" value={title} onChange={(e)=>{settitle(e.target.value)}}  placeholder="Enter your Title"/>
                </div>
                <div className="col-6">
                    <label htmlFor="desc" className="visually-hidden">Desc</label>
                    <input type="text" className="form-control" value={desc} onChange={(e)=>{setdesc(e.target.value)}}   name="desc" placeholder="Desc"/>
                </div>
                <div className="col-2">
                    <button type="submit" className="btn btn-primary mb-3">Add to List</button>
                </div>

            </form>
            <hr/>
        </div>
    )
}
